@mixin hover($_property, $_toValue) {
  transition: #{$_property} 0.3s;
  &:hover {
    #{$_property}: $_toValue;
  }
}

@mixin onDesktop {
  @media (min-width:1024px) {
    @content;
  }
}

@mixin onTablet {
  @media (min-width:744px) and (max-width:1023px) {
    @content;
  }
}

@mixin onPhone {
  @media (max-width:743px) {
    @content;
  }
}
